@import '../../../../styles//variables.scss';

.this_day{
    padding: 20px;
    max-width: 400px;
    width: 100%;
    background: var(#{--components-background-default});
    box-shadow: var(#{--card-shadow-default});
    border-radius: $border-radius;
}

.top_block{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;

    svg{
        width: 160px;
        height: 160px;
    }

    .this_temp{
        font-size: 96px;
        font-weight: 500;
        color: $primary-color;
    }

    .this_day_name{
        font-size: 40px;
        color: var(#{--text-color-default});
    }
}

.bottom_block{
    .this_time{
        margin-bottom: 15px;
    }
    .this_time, .this_city{
        color: $base-color;
        font-size: 25px;
    }
}

@media screen and (max-width:1250px) {
    .this_day{
        max-width: 300px;
    }
}


@media screen and (max-width:1060px) {
    .top_block{
        margin-bottom: 20px;

        .this_temp{
            font-size: 72px;
        }

        .this_day_name{
            font-size: 32px;
        }

        svg{
            width: 110px;
            height: 110px;
        }
    }
    .bottom_block{
        .this_time, .this_city{
            font-size: 20px;
        }
    }
}

@media screen and (max-width: 784px){
    .this_day{
        max-width: 100%;
        margin-bottom: 20px;
    }
}


